.product {
    display: flex;
    padding: 8px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    gap: 8px;
}

.info {
    flex: 1;
}

.title {
    font-size: 20px;
    line-height: 1.2;
    margin: 0 0 8px 0;
}

.price {
    font-size: 16px;
    margin: 0 0 16px 0;
}
